//variables
//$navBackground: #5454e2
$navBackground: #1616b0;
$footerBackground: adjust-hue($navBackground, -20deg);

@import 'GlobalComponents/Navigation';
@import 'GlobalComponents/MobileNavigation';
@import 'GlobalComponents/Footer';

@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content; }
  @-moz-keyframes #{$name} {
    @content; }
  @-ms-keyframes #{$name} {
    @content; }
  @keyframes #{$name} {
    @content; } }

@include keyframes(imageAnimation) {
  0% {
    background-position: left top; }
  100% {
    background-position: right bottom; } }

#root {
  background: url("../Images/homePageImages/weather2.jpeg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  animation: imageAnimation 20s linear infinite alternate;
  background-size: cover; }

a {
  text-decoration: none; }

//custom scrollbar
::-webkit-scrollbar {
  width: 10px; }


/* Track */
::-webkit-scrollbar-track {
  background: #447ad9; }


/* Handle */
::-webkit-scrollbar-thumb {
  background: #0f1ae3; }


/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #0f0a4f; }
